<template>
    <el-card class="form-container">
        <!--步骤-->
        <el-steps :active="active" finish-status="success" align-center>
            <el-step title="填写商品信息"></el-step>
            <el-step title="填写商品促销"></el-step>
            <el-step title="填写商品属性"></el-step>
        </el-steps>
        <product-info-detail
                v-show="showStatus[0]"
                v-model="productObj"
                :is-update="isUpdate"
                @nextStep="nextStep"
        ></product-info-detail>
        <product-sale-detail
                v-show="showStatus[1]"
                v-model="productObj"
                :is-update="isUpdate"
                @nextStep="nextStep"
                @prevStep="prevStep"
        ></product-sale-detail>
        <product-attr-detail
                v-show="showStatus[2]"
                v-model="productObj"
                :is-update="isUpdate"
                @prevStep="prevStep"
                @finishCommit="finishCommit"
        >

        </product-attr-detail>
    </el-card>
</template>

<script>
    import ProductInfoDetail from './productInfoDetail'
    import productSaleDetail from './productSaleDetail'
    import productAttrDetail from './productAttrDetail'
    import {addProduct, getProductById, editProductById} from './../../../../api/productApi'

    const defaultProductObj = {
        // 商品分类
        productCategoryId: null,
        productCategoryName: '',
        name: '', // 商品名称
        subTitle: '',
        productIntro: '', // 商品介绍
        productSn: '', // 商品货号
        originalPrice: null, // 市场价
        price: null, // 商品售价
        store: null, // 商品库存
        sort: null,
        giftScore: null,
        giftGrow: null,
        publishStatus: 1, // 是否上架
        newsStatus: 1,
        recommendStatus: 1,
        serviceIds: '', // 服务列表
        netContent: '', // 净含量
        storageCondition: '', // 储存条件
        quality: '', // 保质期
        reductionType: 0, // 优惠选项
        reductionPrice: 0, // 促销价格
        reductionStartTime: null, //特惠开始时间
        reductionEndTime: null, //特惠结束时间
        // 会员体系价格
        memberPriceList: [
            {
                memberLevelId: 1,
                memberLevelName: '黄金会员',
                memberLevelPrice: null
            },
            {
                memberLevelId: 2,
                memberLevelName: '白金会员',
                memberLevelPrice: null
            },{
                memberLevelId: 3,
                memberLevelName: '钻石会员',
                memberLevelPrice: null
            }
        ],
        // 首页秒杀
        productHomeKillList: [
            {count: 0, discount: 0}
        ],
        // 商品主图片
        pic: '',
        // 商品图片集
        albumPics: '',
        // 详情页
        detailHtml: ''
    };
    export default {
        name: "productDetail",
        data(){
            return {
                active: 0,
                showStatus: [true, false, false],
                // 商品参数
                productObj: Object.assign({}, defaultProductObj)
            }
        },
        props:{
            // 判断是编辑还是添加
            isUpdate: {
                type: Boolean,
                default: false
            }
        },
        components: {
            ProductInfoDetail,
            productSaleDetail,
            productAttrDetail
        },
        created(){
           if(this.isUpdate){ // 编辑
               getProductById({id: this.$route.query.id}).then((response)=>{
                   console.log(response);
                   if(response.status === 1){
                       let tempProductObj = response.data;
                       // 特殊处理 会员价格 和 首页秒杀
                       if(tempProductObj.memberPriceList.length === 0){
                           tempProductObj.memberPriceList = [
                               {
                                   memberLevelId: 1,
                                   memberLevelName: '黄金会员',
                                   memberLevelPrice: null
                               },
                               {
                                   memberLevelId: 2,
                                   memberLevelName: '白金会员',
                                   memberLevelPrice: null
                               },{
                                   memberLevelId: 3,
                                   memberLevelName: '钻石会员',
                                   memberLevelPrice: null
                               }
                           ]
                       }
                       if(tempProductObj.productHomeKillList.length === 0){
                           tempProductObj.productHomeKillList =[
                               {count: 0, discount: 0}
                           ]
                       }
                       this.productObj = response.data;
                       console.log(response.data, this.productObj);
                   }
               }).catch((error)=>{
                   this.$message({
                       message: error.msg,
                       type: 'error',
                       duration: 1000
                   });
               });
           }
        },
        methods: {
            hideAll(){
               for (let i=0; i< this.showStatus.length; i++){
                   this.showStatus[i] = false
               }
            },
            // 上一步
            prevStep(){
                if(this.active > 0 && this.active < this.showStatus.length){
                     this.active--;
                     this.hideAll();
                    this.showStatus[this.active] = true;
                }
            },
            // 下一步
            nextStep(){
               if(this.active < this.showStatus.length - 1){
                   this.active++;
                   this.hideAll();
                   this.showStatus[this.active] = true;
               }
            },
            // 提交
            finishCommit(isUpdate){
                this.$confirm('是否创建一个新的商品', '小撩温馨提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    if(isUpdate){ // 编辑
                        editProductById(this.productObj).then(response => {
                            console.log(response);
                            if(response && response.status === 1){
                                this.$message({
                                   type: 'success',
                                   message: response.msg,
                                   duration: 1000
                                });
                                // 路由跳转
                                this.$router.push('/pm/product');
                            }

                        }).catch((error)=>{
                            this.$message({
                                type: 'error',
                                message: error.msg,
                                duration: 1000
                            });
                        });
                    }else { // 添加
                        addProduct(this.productObj).then(response=>{
                            if(response && response.status === 1){
                                this.$message({
                                    message: response.msg,
                                    type: 'success',
                                    duration: 1000
                                });
                                // 重新加载页面
                                location.reload();
                            }else {
                                this.$message({
                                    message: response.msg,
                                    type: 'error',
                                    duration: 1000
                                });
                            }
                        })
                    }
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消'
                    });
                });
            }
        }
    }
</script>

