<template>
    <div style="margin-top: 50px;">
        <el-form
                ref="productCateForm"
                :model="value"
                label-width="150px"
                style="width: 800px"
        >
            <el-form-item label="商品相册: ">
                <multi-upload
                        upload-action="/manager/api/auth/product/upload_product"
                        v-model="dealProductPic"
                />
            </el-form-item>
            <el-form-item label="商品详情页: ">
                <quill-editor
                        ref="phoneEditor"
                        v-model="value.detailHtml"
                        @blur="onEditorBlur($event)"
                        @focus="onEditorFocus($event)"
                        @ready="onEditorReady($event)"
                />
            </el-form-item>
            <el-form-item>
                <el-button size="medium" @click="handlePrev">上一步, 填写商品促销</el-button>
                <el-button type="primary" size="medium" @click="handleCommit">完成, 提交商品</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import MultiUpload from './../../../../components/upload/multiUpload'

    export default {
        name: "productAttrDetail",
        props:{
            value: Object,
            // 判断是编辑还是添加
            isUpdate: {
                type: Boolean,
                default: false
            }
        },
        data() {
            return {}
        },
        components: {
            MultiUpload
        },
        computed: {
            // 商品的主图和轮播图片
            dealProductPic: {
                get() {
                    let pics = [];
                    // 1. 验证主图
                    if (this.value.pic === null || this.value.pic === undefined || this.value.pic === '') {
                        return pics;
                    }
                    // 2. 存在主图
                    pics.push(this.value.pic);
                    // 3. 验证录播图
                    if (this.value.albumPics === null || this.value.albumPics === undefined || this.value.albumPics === '') {
                        return pics;
                    }
                    // 4. 存在轮播图
                    let albumPicsArr = this.value.albumPics.split(',');
                    for (let i = 0; i < albumPicsArr.length; i++) {
                        pics.push(albumPicsArr[i]);
                    }

                    return pics;
                },
                set(newValue) {
                    if(newValue === null || newValue.length === 0){
                        this.value.pic = null;
                        this.value.albumPics = null;
                    }else {
                        this.value.pic = newValue[0];
                        this.value.albumPics = '';
                        if(newValue.length > 1){
                            for(let i=1; i<newValue.length; i++){
                                this.value.albumPics += newValue[i];
                                if(newValue.length - 1 !== i){
                                    this.value.albumPics += ',';
                                }
                            }
                        }
                    }
                }
            }
        },
        methods: {
            handlePrev() {
                this.$emit('prevStep');
            },
            handleCommit() {
                this.$emit('finishCommit', this.isUpdate);
            },
            onEditorBlur(quill) {
                console.log('editor blur!', quill);
                console.log(this.content);
            },
            onEditorFocus(quill) {
                console.log('editor focus!', quill)
            },
            onEditorReady(quill) {
                console.log('editor ready!', quill)
            },
        }
    }
</script>

<style lang="less">
    .ql-editor {
        height: 400px;
    }
</style>
