<template>
    <div style="margin-top: 50px">
        <el-form
                ref="productCateForm"
                :model="value"
                :rules="productInfoRules"
                label-width="150px"
                style="width: 600px"
        >
            <el-form-item label="商品分类" prop="productCategoryId">
                <el-cascader
                        v-model="productCateValue"
                        :options="productCateOptions"
                        :props="{ expandTrigger: 'hover' }"
                        >
                </el-cascader>
            </el-form-item>
            <el-form-item label="商品名称" prop="name">
                <el-input v-model="value.name"></el-input>
            </el-form-item>
            <el-form-item label="副标题" prop="subTitle">
                <el-input v-model="value.subTitle"></el-input>
            </el-form-item>
            <el-form-item label="商品货号：">
                <el-input v-model="value.productSn"></el-input>
            </el-form-item>
            <el-form-item label="市场价：">
                <el-input v-model="value.originalPrice"></el-input>
            </el-form-item>
            <el-form-item label="商品售价：">
                <el-input v-model="value.price"></el-input>
            </el-form-item>
            <el-form-item label="商品库存:">
                <el-input v-model="value.store"></el-input>
            </el-form-item>
            <el-form-item label="排序：">
                <el-input v-model="value.sort"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" size="medium" @click="handleNext('productCateForm')">下一步, 填写商品促销</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import {getCategoryWithChildren} from './../../../../api/categoryApi'

    export default {
        name: "productInfoDetail",
        props:{
            value: Object,
            // 判断是编辑还是添加
            isUpdate: {
                type: Boolean,
                default: false
            }
        },
        computed:{
           // 商品编号
           productId(){
               return this.value.id
           }
        },
        watch: {
            // 编辑状态下监视是否有id
            async productId(newValue){
                // console.log(newValue);
                if(!this.isUpdate) return;
                if(newValue === undefined || newValue === null || newValue === 0) return;

                let result = await getCategoryWithChildren();
                let listArr = result.data;
                this.productCateOptions = [];
                for (let i = 0; i < listArr.length; i++) {
                    let children = [];
                    // 有二级
                    if(listArr[i].children != null && listArr[i].children.length>0){
                        for(let j=0; j<listArr[i].children.length; j++){
                            children.push({value: listArr[i].children[j].id, label: listArr[i].children[j].name});
                        }
                    }
                    children = children.length > 0 ? children : null;
                    this.productCateOptions.push({
                        label: listArr[i].name,
                        value: listArr[i].id,
                        children: children
                    })
                }

                // 调用
                this.handleEdit();
            },

            productCateValue(newValue){
                if(newValue.length === 1){ // 一级

                    this.value.productCategoryId = newValue[0];
                    this.value.productCategoryName = this.getCateNameById(this.value.productCategoryId);

                }else if(newValue.length === 2){ // 二级

                    this.value.productCategoryId = newValue[1];
                    this.value.productCategoryName = this.getCateNameById(this.value.productCategoryId);

                }

                 console.log(this.value.productCategoryId, this.value.productCategoryName);

            }
        },
        data() {
            return {
                productInfoRules: {
                    name: [
                        {required: true, message: '请输入商品的名称', trigger: 'blur'},
                        {min: 3, max: 200, message: '长度在 3 到 200 个字符', trigger: 'blur'}
                    ],
                    subTitle: [
                        {required: true, message: '请输入商品的副标题', trigger: 'blur'}
                    ],
                    productCategoryId:[
                        {required: true, message: '请选择商品的分类', trigger: 'blur'}
                    ]
                },
                // 选中分类的数据
                productCateValue: [],
                // 分类的数据
                productCateOptions: []
            }
        },
        created() {
            this.getCategoryAll();
        },
        methods: {
            // 获取分类数据
            async getCategoryAll(){
                let result = await getCategoryWithChildren();
                let listArr = result.data;
                this.productCateOptions = [];
                for (let i = 0; i < listArr.length; i++) {
                    let children = [];
                    // 有二级
                    if(listArr[i].children != null && listArr[i].children.length>0){
                        for(let j=0; j<listArr[i].children.length; j++){
                            children.push({value: listArr[i].children[j].id, label: listArr[i].children[j].name});
                        }
                    }
                    children = children.length > 0 ? children : null;
                    this.productCateOptions.push({
                        label: listArr[i].name,
                        value: listArr[i].id,
                        children: children
                    })
                }
            },
            // 处理编辑状态下的业务
            handleEdit(){
               if(this.value.productCategoryId !== null){

                   console.log(this.value);
                   // 处理有二级的情况
                   if(this.value.cateParentId && this.value.cateParentId !== 0){

                       this.productCateValue.push(this.value.cateParentId);
                   }
                   this.productCateValue.push(this.value.productCategoryId);
                   console.log(this.value);

               }
            },
            // 根据id获取分类的名称
            getCateNameById(id){
                console.log('=======');
                console.log(id, this.productCateOptions);
                console.log('=======');
                let name = null, pco = this.productCateOptions;

                for(let i=0; i<pco.length; i++){
                    let children = pco[i].children;
                    if(children){ // 二级
                        for(let j=0; j<pco[i].children.length; j++){
                            if(pco[i].children[j].value === id){
                                name = pco[i].children[j].label;
                                return name;
                            }
                        }
                    }else { // 一级
                        if(pco[i].value === id){
                            name = pco[i].label;
                            return name;
                        }
                    }
                }
                return name;
            },
            handleNext(formName) {
                /*
                this.$refs[formName].validate((valid) => {
                    if (valid) {
                        this.$emit('nextStep');
                    } else {
                        this.$message({
                           message: '请把内容填写完整',
                           type: 'warning',
                           duration: 1000
                        });
                        return false;
                    }
                });
                */
                this.$emit('nextStep');
            },
        }
    }
</script>

